:root {
    --schedule-overdue-tint: #ff7066;
    --schedule-empty-tint: #4d4d4d;
}

.DayHeader {
    padding-bottom: 8px;
    padding-left: 8px;
    cursor: pointer;

    &.Selected {
        border-left: 4px solid var(--main-text-color);
        background-color: var(--secondary-bg-color);
    }
}
