.SubActions {
    z-index: 2;
    display: flex;
    gap: 8px;
    flex-wrap: wrap;
    background-color: var(--main-bg-color);
    padding: 16px;
    border-radius: 8px;
    max-width: 75%;
}

.Overlay {
    display: flex;
    position: fixed;
    z-index: 1;
    top:0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: var(--color-overlay-background);
    justify-content: center;
    align-items: flex-start;
}
