.Initialize {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.Loader {
    display: inline-block;
    animation: beatFade 1.5s infinite;
    font-size: 3rem;
}

@keyframes beatFade {
    0%, 100% {
        opacity: 0.4;
        transform: scale(1);
    }
    50% {
        opacity: 1;
        transform: scale(1.25);
    }
}
