.ScheduleDayView {
    cursor: pointer;
    padding: 8px;
    border: solid 1px transparent;
    display: flex;
}
.Today {
    color: var(--main-bg-color);
    background-color: var(--main-text-color);
    border-radius: 50%;
}

.Day {
    font-size: var(--font-size-small);
    padding: 4px;
    margin-left: -4px;
}
.Overdue {
    color: var(--secondary-text-color);
}

.Current {
    border-color: var(--main-text-color);
    border-radius: 4px;
}
.Priorities {
    display: flex;
    flex-wrap: wrap;
    gap: 2px;
    margin-top: 4px;
    margin-left: 4px;
}
