.DurationComp {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    align-items: center;
    color: var(--main-text-color);
    opacity: .8;
}

.Value {
    font-size: var(--font-size-small);
}

.Item {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: var(--main-text-color);

    &.Hours {
        width: 24px;
        border-radius: 2px;
    }
}
