.ExpandableContainer {
    position: relative;
    z-index: 2;
}

.ExpandableHeader {
    cursor: pointer;
}

.ExpandableContent {
    position: absolute;
    background-color: var(--main-bg-color);
    animation: expand 0.3s ease-out;
}

.Overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-overlay-background);
    z-index: 1;
}

@keyframes expand {
    from {
        height: 0;
        opacity: 0;
    }
    to {
        height: auto;
        opacity: 1;
    }
}
