.Tooltip {
    cursor: pointer;
    position: relative;
    display: inline-block;
    &:hover .Content {
        visibility: visible;
    }
    .Content {
        position: absolute;
        white-space: nowrap;
        padding: 8px;
        font-size: var(--font-size-small);
        background-color: var(--secondary-bg-color);
        border-radius: 4px;
        visibility: hidden;
    }
}
