:root {
    --main-bg-color: #1e1e1e;
    --secondary-bg-color: #262626;
    --main-text-color: #ccc;
    --secondary-text-color: #737373;
    --secondary-text-color-warn: #ff4a3d;
    --inpout-placeholder: #808080;
    --font-size-big: 32px;
    --font-size-main: 16px;
    --font-size-small: 12px;
    --color-today: #1c8c3a;
    --color-tomorrow: #eb8600;
    --color-weekend: #297eff;
    --color-next-week: #9147ff;
    --color-overdue: #ff4a3d;
    --color-overlay-background: rgba(0, 0, 0, 0.5);
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    background-color: var(--main-bg-color);
    min-height: 100vh;
    height: 100%;
    font-size: var(--font-size-main);
    color: var(--main-text-color);
}

#root {
    height: 100vh;
}

button, input[type="submit"], input[type="reset"] {
    background: none;
    border: none;
    outline: inherit;
    box-shadow: none;
    cursor: pointer;
    color: inherit;
    padding: 0;
    font: inherit;
}

input {
    box-sizing: border-box;
    background-color: transparent;
    border: 0;
    font-size: 24px;
    appearance: none;
    outline: none;
    color: var(--main-text-color);

    &::placeholder {
        color: var(--inpout-placeholder)
    }
}
input[type="checkbox"] {
    width: 20px;
    height: 20px;
    cursor: pointer;
    accent-color: #4a90e2;
    appearance: auto;
    border: 2px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.3s ease, background-color 0.3s ease;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-large{
    font-size: 24px;
    font-weight: 600;
}.text-small{
    font-size: 12px;
    /*font-weight: 600;*/
}
