.Project {
    border-bottom: 1px solid var(--secondary-text-color);
    padding: 8px;
    display: grid;
    grid-template-areas:
    "main header"
    "footer footer";
    grid-template-columns:  1fr auto;
    grid-template-rows: auto auto;
}

.Main {
    grid-area: main;
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    .Content {
        margin-left: 8px;
    }
}

.Header {
    grid-area: header;
    font-weight: 500;
}

.Footer {
    grid-area: footer;
}

.Subtasks {
    margin-left: 16px;
}
