.TaskItem {
    padding-left: 8px;
    padding-bottom: 8px;
    padding-top: 8px;
    cursor: pointer;

    &.Selected {
        border-left: 4px solid var(--main-text-color);
        background-color: var(--secondary-bg-color);
    }
}

.Header {
    display: flex;
    gap: 8px;
    align-items: center;
    .Content {
        margin-left: 8px;
        display: flex;
        flex-direction: column;
    }
}
