:root {
    --border-radius: 8px;
    --border: 4px solid;
}

.Hotkey {
    display: flex;
    font-size: 1.2rem;
    font-weight: bold;
    border: 4px solid;
    border-radius: 8px;
    border-color: var(--main-text-color);
    &.Disabled {
        cursor: not-allowed;
        opacity: 0.2;
    }
    &.Revert{
        flex-direction: row-reverse;
    }
}

.Binding {
    display: flex;
    align-items: center;
    height: 100%;
    background-color: var(--main-text-color);
    padding-left: 8px;
    padding-right: 8px;
    color: var(--main-bg-color);
    white-space: nowrap;
}

.Value {
    padding-right: 8px;
    padding-left: 8px;
    background-color: transparent;
    white-space: nowrap;
}


@media only screen and (max-width: 800px) {
    .Binding {
        display: none;
    }
}
