.Container {
    position: relative;
}
.Indicator {
    position: absolute;
    top: -4px;
    right: -4px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    box-shadow: 0 0 2px #008000, 0 0 4px #008000,0 0 6px #008000, 0 0 8px #008000, 0 0 10px #008000, 0 0 12px #008000, 0 0 14px #008000, 0 0 16px #008000;
    animation: blinker .9s cubic-bezier(.5, 0, 1, 1) infinite alternate;
}

@keyframes blinker {
    from {
        opacity: 1;
    }
    to {
        opacity: .6;
    }
}
